<template>
  <girl-shoe v-if="menu == 'girl' && submenu == 'shoe'"></girl-shoe>
  <girl-shirt v-if="menu == 'girl' && submenu == 'shirt'"></girl-shirt>
  <girl-trousers v-if="menu == 'girl' && submenu == 'trousers'"></girl-trousers>
  <girl-dress v-if="menu == 'girl' && submenu == 'dress'"></girl-dress>
  <girl-underware v-if="menu == 'girl' && submenu == 'underware'"></girl-underware>
  <girl-bra v-if="menu == 'girl' && submenu == 'bra'"></girl-bra>
  <child-shoe v-if="menu == 'child' && submenu == 'shoe'"></child-shoe>
  <child-wear v-if="menu == 'child' && submenu == 'wear'"></child-wear>
  <boy-shoe v-if="menu == 'boy' && submenu == 'shoe'"></boy-shoe>
  <boy-shirt v-if="menu == 'boy' && submenu == 'shirt'"></boy-shirt>
  <boy-trousers v-if="menu == 'boy' && submenu == 'trousers'"></boy-trousers>
  <boy-jacket v-if="menu == 'boy' && submenu == 'jacket'"></boy-jacket>
  <boy-underware v-if="menu == 'boy' && submenu == 'underware'"></boy-underware>
  <boy-suit v-if="menu == 'boy' && submenu == 'suit'"></boy-suit>

</template>
<script>
  import BoyShoe from './tables/boy-shoe'
  import BoyShirt from './tables/boy-shirt'
  import BoyTrousers from './tables/boy-trousers'
  import BoyJacket from './tables/boy-jacket'
  import BoyUnderware from './tables/boy-underware'
  import BoySuit from './tables/boy-suit'
  import GirlShoe from './tables/girl-shoe'
  import GirlShirt from './tables/girl-shirt'
  import GirlTrousers from './tables/girl-trousers'
  import GirlDress from './tables/girl-dress'
  import GirlUnderware from './tables/girl-underware'
  import GirlBra from './tables/girl-bra'
  import ChildShoe from './tables/child-shoe'
  import ChildWear from './tables/child-wear'

  export default {
    props: ['menu', 'submenu'],
    components: {
      BoyShoe,
      BoyShirt,
      BoyTrousers,
      BoyJacket,
      BoyUnderware,
      BoySuit,
      ChildShoe,
      ChildWear,
      GirlShoe,
      GirlShirt,
      GirlTrousers,
      GirlDress,
      GirlUnderware,
      GirlBra
    }
  }
</script>



